import React from 'react'
import './card.css'
const Card = ({lang, img, fcolor, scolor}) => {
    return (
        <div 
        className='card'
        style= {{
            background:`linear-gradient(to left, ${fcolor}, ${scolor})`,
        }}
        >
            
           <img src={img} alt='lang.svg'/> 
           <h3>{lang}</h3>
        </div>
    )
}

export default Card
